const PostContainer = styled.div`
  text-align: left;
`;

const StyledIcon = styled.img`
  display: inline-block;
  height: 16px;
  margin: 0 0 -2px;
  padding: 0px;
  width: 16px;
`;

const StyledDate = styled.span`
  color: #0057a8;
  line-height: 30px;
  margin: 0;
  padding: 0px 10px;
`;
